.jumbotron {
	background-image: url("http://minted-uk.com/wp-content/uploads/2018/06/Looney-Tunes-Banner.jpg");
    background-blend-mode: lighten;
    background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	margin-bottom: 0px;
    text-align: center;
}

.jumbotron h1 {
	font-family: 'Montserrat', sans-serif;
	font-size: 65px;
	text-transform: uppercase;
	font-weight: 900;
	color: rgb(255, 123, 0);
}

.jumbotron p {
	font-family: 'Montserrat', sans-serif;
	font-size: 18px;
	font-weight: 500;
	text-transform: uppercase;
}
.allCards {
    margin-bottom: 10px;
    text-align: center;
}

.allCards img {
    margin: 5px;
    padding: 20px;
    border: 1px solid rgba(0, 0, 0, 0.25);
    max-height: 200px;
    max-width: 200px;
}
.gameMessage {
    text-align: center;
    max-width: 100%;
    -webkit-align-content: center;
            align-content: center
}

.gameMessage p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color: rgb(228, 205, 1);
    color: white;
    padding: 20px 0;
    margin-bottom: 0px;
}

.gameScores {
    text-align: center;
    max-width: 100%;
    -webkit-align-content: center;
            align-content: center;
    margin-bottom: 10px;
}
.gameScores p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color:#d47405;
    color: white;
    padding: 20px 0;
    margin-bottom: 25px;
}
.mainSection {
    padding: 0; 
    background-color: rgba(199, 66, 5, 0.03);
    min-height: 100vh;
    padding-bottom: 100px;
    }
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

